import React from 'react';
import Layout from '../components/layout';
import SEO from '../components/seo';
import Collapsible from 'react-collapsible';
import SectionTitles from '../components/shared/section-titles';
import constants from '../constants/coverage';
import GoogleMapComponent from '../components/shared/google-map';
import BottomCta from "../components/shared/bottom-cta"
import { Link } from 'gatsby';
import meta from "../constants/meta-descriptions";

const CoveragePage = () => {

  return (
    <>
      <Layout>
        <SEO
          title={meta.shops.title}
          description={meta.shops.metaDescription}
        />

        <div style={{ paddingTop: '12rem' }}>
          <SectionTitles
            undertext={'             ,        ,  NetSurf Play.'}>
            <h1>    30<br /> </h1>
            <hr className='center' />
          </SectionTitles>

          <div className='container'>
            <div className='my-5'>
              <GoogleMapComponent height={600}
                mapState={{
                  center: {
                    lat: 42.82360981,
                    lng: 23.29376221,
                  },
                  zoom: 8,
                }}
                isRenderPolygon={true}
              />
            </div>
            <div className='d-flex justify-content-center text-center mb-5'><h2> <br /></h2></div>
            <div className='d-flex justify-content-center'>
              <div className='col-12 col-lg-9 col-xl-8'>
                {constants.places.map((place, index) => (
                  <CElement
                    {...place}
                    isInitiallyOpen={index === 0}
                  />
                ))}
              </div>
            </div>

            <BottomCta
              removeSpacing={true}
              bgColor={'transparent'}>
              <h2 className='mb-4 d-none d-md-block'>    <br /><span className='text-purple'>   !</span></h2>
              <h2 className='mb-4 d-block d-md-none'>    <span className='text-purple'>   !</span></h2>
              <p className='mb-4'>   -     </p>
              <Link to={'/za-doma'} className='btn btn-primary green'> </Link>
            </BottomCta>
          </div>
        </div>
      </Layout>
    </>
  )
}

const CElement = ({ mainPlace, subPlaces, isInitiallyOpen }) => {
  return (
    <>
      <Collapsible
        open={isInitiallyOpen}
        trigger={
          <CHeader isOpen={false} places={subPlaces} title={mainPlace} />
        }
        triggerWhenOpen={
          <CHeader isOpen={true} places={subPlaces} title={mainPlace} />
        }
      >
        <CContent
          places={subPlaces}
        />
      </Collapsible>
    </>
  )
}

const CContent = ({ places }) => {
  return (
    <>
      <div className='row'>
        {places.map((place, index) => (
          <div className='col-6 col-md-4' key={place + index}>
            <p><span className='icon map-pin mr-2'></span>{place}</p>
          </div>
        ))}
      </div>
    </>
  );
}

const CHeader = ({ isOpen, title, places }) => {
  return (
    <>
      <div className='row w-100 mt-2'>
        <button className='clear w-100'>
          <div className='d-flex justify-content-between mb-0'>
            <h3 className={'text-left'}><span className='icon green-tick mr-2'></span>{title}</h3>
            <p className={'text-left'}>+{places.length} <span className={'icon ml-2 ' + (!isOpen ? 'arrow-close' : 'arrow-open')}></span></p>
          </div>
          <hr className='normal-bottom m-0 p-0' />
        </button>
      </div>
    </>
  )
}


export default CoveragePage;